import http from '@/api/http.js'
export default{
	data(){
		return{
			info:{},
			listid:0
		}
	},
	onLoad(option) {
		this.listid = option.id
		this.getList()
	},
	methods:{
		getList(){
			let data = {
				id:this.listid
			}
			http('/api/Setting/aboutUsDetails','post',data).then(res=>{
				this.info = res.data.data
				console.log(res)
			})
		},
		
	}
}
